.cta {
  &__container {
    padding-top: 10rem;
    width: 100%;
    max-width: 157.5rem;
    margin: 0 auto;
    border-radius: 5rem;
    background: linear-gradient(24deg, #ecf3fb 0%, #c2d4e9 100%);
  }

  &__suptitle {
    margin-bottom: 1rem;
  }
  &__title {
    text-align: left;
    margin-bottom: 3rem;
    span {
      background: none;
      color: #ce3c46;
    }
  }

  &__list {
    margin-bottom: 5rem;
  }
}
